import React from 'react';

function NotWorkingForm() {
    const csrftoken = window.CSRF_TOKEN;
    return (
        <div className="notworking-form">
        <form method="post" className="form-input">
            <input name="csrfmiddlewaretoken" value={csrftoken} hidden />
            <input className="inputext" type="text" name="username" placeHolder="Feriado" required />
            <input className="inputdate" type="date" name="username" required />
            <button type="submit" className="btn btn-outline-success"><i class="bi bi-check-lg"></i></button>
        </form>
        </div>
    )
}

export default NotWorkingForm;